/* MAIN */

html, body {
    background: #f1f1f1;
}

#wpbody-content h1 {
    font-size: 1em;
    text-transform: capitalize;
    margin: 0;
    padding: 7px 13px;
    border-bottom: 1px solid #e6e6e6;
}

#wpbody-content a {
    color: #3525E6;
}

#wpbody-content .row-actions span.trash a {
    color: #CF1124;
}

#wpbody-content span.default {
    color: #b9b6c5;
}

aside,
main > section {
    border: 2px solid #e6e6e6;
    background: #fff;
    margin: 7px;
    border-radius: 7px;
}

aside {
    margin-left: 0;
    margin-right: 0;
}

aside > *,
main > section > * {
    padding: 0 13px;
}

/* BREADCRUMB NAV */

nav#hierarchy-breadcrumb a {
    margin-left: 3px;
    text-decoration: none;
}

nav#hierarchy-breadcrumb a:hover {
    text-decoration: underline;
}

/* SUBGROUPS */



/* PAGES */

main section h1 {
    background: #f1f1f1;
}

.post-row-header {
    padding: 5px 13px;
    color: #22272d;
}

.post-row {
    padding: 7px 13px 9px;
    border-top: 1px solid #e6e6e6;
}

.post-row p,
.post-row .row-actions {
    margin: 5px 0;
}

.post-row .post-author img {
    border-radius: 100%;
    border: 1px solid #ccc;
    margin-right: 7px;
}

